import Vue from "vue";
import Router from "vue-router";
import "whatwg-fetch";
import App from "./App.vue";
import "./utils/iconfont.js";
Vue.use(Router);
const router = new Router({
	mode : "history",
	routes : [
		{
			path : "/",
			component : require("./pages/Home"),
			meta : {
				title : "首页"
			}
		},
		{
			path : "/signUp",
			component : require("./pages/SignUp"),
			meta : {
				title : "注册"
			}
		},
		{
			path : "/signIn",
			component : require("./pages/SignIn"),
			meta : {
				title : "登录"
			}
		},
		{
			path : "/me",
			component : require("./pages/Me"),
			meta : {
				title : "我的账户"
			}
		},
		{
			path : "/chat",
			component : require("./pages/chat"),
			meta : {
				title : "消息"
			}
		}
	]
});
router.afterEach(route => {
	document.title = route.meta.title;
});
new Vue({
	el : "#app",
	router,
	render : h => h(App)
});


