<style lang="stylus" scoped>
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	.dialog
		position fixed
		top 50%
		left @top
		padding 2rem .625rem .625rem
		width 12.5rem
		background-color rgba(black, .85)
		border 1px solid $black2
		border-radius .25rem
		color $grey1
		transform translate(-50%, -50%)
		.close
			position absolute
			top .625rem
			right @top
			font-size 1.25rem
			cursor pointer
			&:hover
				color red
		.note
			text-align center
			word-break break-all
			font-size .875rem
			line-height 1.25rem
</style>
<template>
	<transition name="fade">
		<div class="dialog" v-if="message">
			<i class="close" @click="close">×</i>
			<p class="note">{{message}}</p>
		</div>
	</transition>
</template>
<script>
	let t;
	export default {
		data(){
			return {
				message : ""
			};
		},
		methods : {
			close(){
				clearTimeout(t);
				this.message = "";
			}
		},
		updated(){
			clearTimeout(t);
			t = setTimeout(() => {
				this.message = "";
			}, 666);
		}
	};
</script>


