<style lang="stylus">
	*
		margin 0
		padding 0
		line-height 1
		font-family monospace, Microsoft Yahei
	a
		text-decoration none
		color inherit
	h1
	h2
	h3
	h4
	h5
	h6
		font-weight normal
	ul
	ol
		list-style none
	em
	i
		font-style normal
	input
	select
		outline none
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	$orange = rgb(255, 96, 48)
	.icon
		fill currentColor
		overflow hidden
	.btn
		margin-right auto
		margin-left @margin-right
		display block
		width 100%
		height 2.5rem
		border none
		background-color $orange
		text-align center
		font-size 1rem
		line-height @height
		color white
		cursor pointer
		transition background-color .5s
		&:hover
			background-color red
	.fade-enter-active
		transition all .15s ease
	.fade-leave-active
		transition all .25s cubic-bezier(1, .5, .8, 1)
	.fade-enter
	.fade-leave-active
		opacity 0
</style>
<template>
	<div class="app">
		<my-header ref="header" />
		<router-view></router-view>
		<my-footer v-if="footer" ref="footer" />
		<my-dialog ref="dialog" />
	</div>
</template>
<script>
	import MyHeader from "./components/Header";
	import MyFooter from "./components/Footer";
	import MyDialog from "./components/Dialog";
	export default {
		data(){
			return {
				footer : 0
			};
		},
		components : {
			MyHeader,
			MyFooter,
			MyDialog
		},
		beforeCreate(){
			const setRem = onresize = () => {
				document.documentElement.style.fontSize = innerWidth / 20 + "px";
			};
			setRem();
		},
		mounted(){
			this.$refs.header.title = this.$route.meta.title;
		},
		updated(){
			this.$refs.header.title = this.$route.meta.title;
		}
	};
</script>


