<style lang="stylus" scoped>
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	$orange = rgb(255, 96, 48)
	$green = rgb(180, 234, 102)
	.chatWindow
		position fixed
		top 0
		box-sizing border-box
		width 20rem
		height 100%
		padding-top 2.5rem
		padding-bottom @padding-top
	ul
		box-sizing border-box
		padding-right 1rem
		padding-left @padding-right
		padding-bottom 1rem
		height 100%
		overflow-y scroll
		background-color $grey2
		li
			float left
			margin-top .5rem
			width 18rem
			h1
				text-align center
				span
					padding .25rem .5rem
					background-color $grey3
					border-radius .25rem
					font-size .6rem
					color white
					vertical-align middle
			.chat
				margin-top @margin-top
				display flex
				&.mine
					flex-direction row-reverse
					p
						margin-right .75rem
						background-color $green
						&:before
							right -.375rem
							border-width .5rem 0 .5rem .5rem
							border-color transparent transparent transparent $green
				&:not(.mine)
					p
						margin-left .75rem
						background-color white
						&:before
							left -.375rem
							border-width .5rem .5rem .5rem 0
							border-color transparent white transparent transparent
				.head
					width 2rem
					height @width
					background-size 100% 100%
				p
					position relative
					padding .5rem
					max-width 10rem
					border-radius .25rem
					font-size .9rem
					line-height 1.2rem
					word-break break-all
					&:before
						position absolute
						top .625rem
						content ""
						border-style solid
	.message
		position absolute
		bottom 0
		padding-top .35rem
		padding-bottom @padding-top
		width 20rem
		background-color $grey2
		border-top 1px solid $grey2 - 15
		input
		a
			height 1.8rem
			border none
			vertical-align middle
			font-size .8rem
		.msg
			margin-left .5rem
			padding-right .5rem
			padding-left @padding-right
			width 13.5rem
		.send
			display inline-block
			width 4rem
			background-color $orange
			text-align center
			line-height 1.8rem
			color white
</style>
<template>
	<div class="chatWindow">
		<ul ref="chatLog">
			<li v-for="item in messages">
				<h1>
					<span>{{item.time||`${item.user}${["退出","加入"][item.status]}群聊`}}</span>
				</h1>
				<div v-if="item.time" :class="{chat:1,mine:item.isMine}">
					<a class="head" :style="{backgroundImage:item.head}"></a>
					<p>{{item.message}}</p>
				</div>
			</li>
		</ul>
		<div class="message">
			<input class="msg" v-model="message" @keyup.enter="chat" ref="input" />
			<a class="send" @click="chat">发送</a>
		</div>
	</div>
</template>
<script>
	import io from "socket.io-client";
	let chat;
	const dateFormat = timestamp => {
		const date = new Date(timestamp);
		const format = num => `${["0", ""][+(num > 9)]}${num}`;
		return `${date.getFullYear()}年${format(date.getMonth() + 1)}月${format(date.getDate())}日 ${format(date.getHours())}:${format(date.getMinutes())}`;
	};
	export default {
		data(){
			return {
				user : "",
				message : "",
				messages : []
			};
		},
		methods : {
			chat(){
				if(/\S/.test(this.message)){
					chat.emit("message", this.message);
					this.message = "";
				}else{
					this.$parent.$refs.dialog.message = "不能发送空白消息";
					this.message = "";
				}
			}
		},
		beforeCreate(){
			this.$parent.footer = 0;
		},
		created(){
			const {
				header
			} = this.$parent.$refs;
			chat = io("/chat");
			chat
				.once("connections", ({user, status}) => {
					this.user = user;
				})
				.on("connections", ({user, status, online}) => {
					header.title = `${this.$route.meta.title}（${status ? online : online - 1}）`;
					this.messages.push({
						user,
						status
					});
				})
				.on("message", async ({user, message, time, head}) => {
					const isMine = this.user === user;
					this.messages.push({
						message,
						time : dateFormat(time),
						head : `url(${head})`,
						isMine
					});
					if(isMine){
						return;
					}
					if(await Notification.requestPermission().then() === "granted"){
						let t;
						const note = new Notification(user, {
							icon : head,
							body : message
						});
						note.onshow = e => {
							t = setTimeout(() => {
								e.target.close();
							}, 2333);
						};
						note.onclose = () => {
							clearTimeout(t);
						};
					}
				});
		},
		updated(){
			const chatLog = this.$refs.chatLog;
			chatLog.scrollTop = chatLog.scrollHeight - chatLog.offsetHeight;
		},
		beforeDestroy(){
			chat.disconnect();
		}
	};
</script>


