<style lang="stylus">
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	footer
		position fixed
		bottom 0
		z-index 1
		display flex
		width 20rem
		height 2.5rem
		background-color $grey2
		text-align center
		line-height 2.5rem
		color $black2
		a
			display flex
			flex 1
			align-items center
			justify-content center
			flex-direction column
			.icon
				width 1.25rem
				height @width
			span
				margin-top .15rem
				font-size .6rem
</style>
<template>
	<footer>
		<router-link v-for="item in menu" :to="item.href">
			<svg class="icon" aria-hidden="true">
				<use :xlink:href="item.icon"></use>
			</svg>
			<span>{{item.name}}</span>
		</router-link>
	</footer>
</template>
<script>
	export default {
		data(){
			return {
				menu : [
					{
						name : "消息",
						href : "/chat",
						icon : "#icon-comments"
					},
					{
						name : "全部",
						href : "/all",
						icon : "#icon-all"
					},
					{
						name : "我",
						href : "/me",
						icon : "#icon-account"
					}
				]
			};
		}
	};
</script>


