<style lang="stylus">
	$black1 = rgb(51, 51, 51)
	$black2 = $black1 * 2
	$grey4 = $black1 * 3
	$grey3 = $black1 * 4
	$grey2 = $black1 / 3 * 14
	$grey1 = $black1 / 3 * 15 - 10
	header
		position relative
		z-index 1
		height 2.5rem
		background-color $black1
		text-align center
		line-height 2.5rem
		color white
		.icon
			position absolute
			top .625rem
			left @top
			width 1.25rem
			height @width
</style>
<template>
	<header>
		<svg class="icon" aria-hidden="true" @click="back">
			<use xlink:href="#icon-arrowleft"></use>
		</svg>
		<span>{{title}}</span>
	</header>
</template>
<script>
	export default {
		data(){
			return {
				title : ""
			};
		},
		methods : {
			back(){
				this.$router.go(-1);
			}
		}
	};
</script>


